

<template>
  <div>
    
    <div class="member-box">
      <!-- <div style="margin-bottom:50px ;">
        <el-page-header @back="goBack" content=""> </el-page-header>
      </div> -->
      <div class="member-header">
        <h1>{{ memberInfos.title }}</h1>
        <p>{{ memberInfos.name }}|{{ memberInfos.company }}</p>
        <p>{{ memberInfos.describe }}</p>
      </div>
      <div class="member-video">
        <video :src="'./static/image/' + memberInfos.video" controls></video>
      </div>
      <h1>{{ $t("Membershipstyle.text") }}</h1>
      <div class="Membershipstyle-view">
        <div
          v-for="(item, index) in memberList"
          :key="index"
          @click="onDateils(item)"
          class="Membershipstyle-item"
        >
          <div class="video">
            <img :src="'./static/image/' + item.cover" alt="" />
            <img src="@/assets/Membershipstyle/icon.png" alt="" />
          </div>
          <div class="content">
            <p>{{ item.title }}</p>
            <p>{{ item.name }} | {{ item.company }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: () => {
    return { memberInfos: {}, memberList: [], list: [] };
  },
  computed: {},

  watch: {
    $route: {
      handler(to, from) {
        const id = this.$route.query.id;
        if (id) {
          this.intData(id);
        }
      },
      immediate: true,
    },
  },
  created() {
    const id = this.$route.query.id;
    this.intData(id);
  },
  methods: {
    onDateils(item) {
      window.scrollTo({ top: 0 });
      this.$router.push({ path: "/member", query: { id: item.id } });
    },
    intData(id) {
      const lang = localStorage.getItem("lang");
      switch (lang) {
        case "en":
          axios.get("/static/lang/en.json", {}).then(({ data }) => {
            console.log("获取到的json数据", data);
            this.list = data.MembershipstyleList;
            const infos = this.list.filter((item) => item.id == id);

            if (infos.length > 0) {
              this.memberInfos = infos[0];
              console.log(this.memberInfos, "===");
            }
            this.memberList = this.list.filter((item) => item.id != id);
          });
          break;
        case "Czh":
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/Czh.json", {}).then(({ data }) => {
            console.log("获取到的json数据", data);
            this.list = data.MembershipstyleList;
            const infos = this.list.filter((item) => item.id == id);

            if (infos.length > 0) {
              this.memberInfos = infos[0];
              console.log(this.memberInfos, "===");
            }
            this.memberList = this.list.filter((item) => item.id != id);
          });

          break;

        default:
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/zh.json", {}).then(({ data }) => {
            console.log("获取到的json数据", data);
            this.list = data.MembershipstyleList;
            const infos = this.list.filter((item) => item.id == id);

            if (infos.length > 0) {
              this.memberInfos = infos[0];
              console.log(this.memberInfos, "===");
            }
            this.memberList = this.list.filter((item) => item.id != id);
          });

          break;
      }
    },
    goBack() {
      this.$router.go(-1)
    }
  },
};
</script>
